import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';

import { Angular2Apollo } from 'angular2-apollo';
import gql from 'graphql-tag';
import 'rxjs/add/operator/toPromise';

/**
 * Generated class for the HashtagPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-hashtag',
  templateUrl: 'hashtag.html',
})
export class HashtagPage {

  hashtag = <any>{};
  squeels = <any>[];

  constructor(public navCtrl: NavController, public navParams: NavParams, public apollo: Angular2Apollo) {
    this.hashtag = this.navParams.get('hashtag');
    this.apollo.query({
      query: gql`
        query allSqueels($id: ID) {
          allSqueels(filter: {hashtag: {id: $id}}) {
            id
            description
            createdAt
            upvotes {
              id
            }
            user {
              id
              profileUrl
              username
            }
            comments {
              id
              comment
              createdAt
              user{
                id
                username
              }
            }
          }
        }
      `, variables: {
        id: this.hashtag.id
      }
    }).toPromise().then(({data}) => {
      this.squeels = data;
      this.squeels = this.squeels.allSqueels;
    })
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad HashtagPage');
  }

}



